        /**
         * Загрузка данных из LocalStorage
         */
        checkLocalState() {
            try {
                if (!this.$bRoute.meta.save_local_storage_l && this.localState) this.removeStateLocal();
                if (!this.localState) return false;
                let answer = confirm('Загрузить данные из LocalStorage');
                answer ? this.models = this.localState.models : this.removeStateLocal();
            } catch (error) {
                console.error('Ошибка ' + error.name + ":" + error.message + "\n" + error.stack);
            }
            ;
        },
        /**
         * Удалить страницу + данные из LocalStorage
         */
        removeStateLocal() {
            try {
                this.$store.commit('removeCardPage', this.$bRoute.fullPath)
            } catch (error) {
                console.error('Ошибка ' + error.name + ":" + error.message + "\n" + error.stack);
            }
            ;
        },
        /**
         * Таймер для сохранения данных в LocalStorage по заданому времени
         * @param {boolean} stopTimer - Остановить таймер если передали TRUE
         */
        timerStore(stopTimer = false) {
            try {
                let timeout = +this.$store.getters.localTimeout * 1000;

                if (!stopTimer && this.timerId === 0) {
                    this.timerId = setInterval(() => {
                        this.$store.dispatch('cardState', {models: this.models, page: this.$bRoute.fullPath})
                        this.$toast.success('Сохранение в LocalStorage', '', {position: 'topRight'});
                        console.log(this.$store.getters.cardsList)
                    }, timeout)
                }
                if (stopTimer) {
                    clearInterval(this.timerId);
                }
            } catch (error) {
                console.error('Ошибка ' + error.name + ":" + error.message + "\n" + error.stack);
            }
        },
        /**
         * Реагирует на нажатие кнопок внизу страницы
         * @param {object} action - обьект, описывающий действия. Пробрасывается из сета возможных действий
         */
        saveModel: async function (action) {
            if (action.code == 'back') {

                if (this.model_has_changed) {
                    this.$toast.error('Изменения не были сохранены', '', {position: 'topRight'})
                    this.$bRouter.go(-1);
                    return;
                } else {
                    this.$bRouter.go(-1);
                    return;
                }
            }


            var result = await this.$validator.validateAll();
            if (result) {
                this.disableButtons(true);
                for (var item in this.fieldsVV) {
                    this.fieldsVV[item].touched = true;
                }
                let data = {
                    main_data_models: this.models,
                    form_parameters: {
                        form_base_data_model: this.form_base_data_model
                    },
                };
                await this.$http.post(action.link, data)
                    .then(async res => {
                        this.timerStore(true); // Сбросить таймер
                        this.removeStateLocal(); // Удалить данные из LocalStorage
                        for (var item in this.fieldsVV) {
                            this.fieldsVV[item].touched = false;
                        }
                        this.$store.dispatch('showToast', {
                            template: res.data.template ? res.data.template : 'success',
                            title: res.data.title,
                            message: res.data.message,
                            position: res.data.position,
                        })
                        if (action.code !== 'receive_documents_request' && action.code !== 'send_request') {
                            var paths = this.$bRoute.path.split('/');
                            this.$bRouter.replace(paths.slice(0, paths.length - 1).join('/') + '/' + res.data.id); // убираем '/new' из конца роута и заменяем его на id созданной карточки
                        }
                        if (res.data.requery && action.code !== 'ok') {

                            this.$nextTick(async () => {
                                this.paramCard.id = (this.step_l) ? this.row_id_fe_route_step_object : this.$bRoute.params[this.$bRoute.meta.id_field];
                                this.loading = true;
                                await this.fillComponent(this.$bRoute.meta.route, this.paramCard);
                                this.loading = false;
                            });
                        }
                        this.model_has_changed = false;
                        this.watch_model_changing = false;
                        this.loading = false;
                        this.disableButtons(false);
                        if (action.code == 'ok' || action.code === 'receive_documents_request') this.$bRouter.go(-1);

                    })
                    .catch(error => {
                        this.loading = false;
                        this.disableButtons(false);
                        result = false;
                        this.$store.dispatch('showToast', {
                            template: 'error',
                            title: error.response.status,
                            message: error.response.data.message,
                            position: error.response.data.position,
                        })
                        if (error.response.data.requery) {

                            this.$nextTick(async () => {
                                this.paramCard.id = (this.step_l) ? this.row_id_fe_route_step_object : this.$bRoute.params[this.$bRoute.meta.id_field];
                                await this.fillComponent(this.$bRoute.meta.route, this.paramCard);
                            });
                        }
                        if (error.response.status !== 400) this.$bRouter.replace({path: '/' + error.response.status})

                    });

//                    if (action.code == 'ok') this.$bRouter.go(-1);
            } else {
                for (var item in this.fieldsVV) {
                    this.fieldsVV[item].touched = true;
                }
                console.log(this.fieldsVV)
                this.$toast.error('Некорректные данные', '', {position: 'topRight'})
            }

            return result;

        },

